<template><undefined>

<h1>Tabs 标签页</h1>
<p>选项卡切换组件。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>提供平级的区域将大块内容进行收纳和展现，保持界面整洁。</p>
<p>Ant Design 依次提供了三级选项卡，分别用于不同的场景。</p>
<ul>
<li>卡片式的页签，提供可关闭的样式，常用于容器顶部。</li>
<li>标准线条式页签，用于容器内部的主功能切换，这是最常用的 Tabs。</li>
<li><a href="/#/components/radio">RadioButton</a> 可作为更次级的页签来使用。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-tabs active-tab-key=\&quot;key1\&quot; @change=\&quot;onChange\&quot; @tab-click=\&quot;onTabClick\&quot;&gt;\n        &lt;v-tab-pane tab-key=\&quot;key1\&quot; tab=\&quot;选项卡一\&quot;&gt;选项卡一内容&lt;/v-tab-pane&gt;\n        &lt;v-tab-pane tab-key=\&quot;key2\&quot; tab=\&quot;选项卡二\&quot;&gt;选项卡二内容&lt;/v-tab-pane&gt;\n        &lt;v-tab-pane tab-key=\&quot;key3\&quot; tab=\&quot;选项卡三\&quot;&gt;选项卡三内容&lt;/v-tab-pane&gt;\n    &lt;/v-tabs&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        methods: {\n            onChange(tabKey) {\n                console.log(\&quot;tab changed : \&quot; + tabKey);\n            },\n            onTabClick(tabKey) {\n                console.log(\&quot;tab clicked : \&quot; + tabKey);\n            }\n        }\n    };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-tabs active-tab-key="key1" @change="onChange" @tab-click="onTabClick">
        <v-tab-pane tab-key="key1" tab="选项卡一">选项卡一内容</v-tab-pane>
        <v-tab-pane tab-key="key2" tab="选项卡二">选项卡二内容</v-tab-pane>
        <v-tab-pane tab-key="key3" tab="选项卡三">选项卡三内容</v-tab-pane>
    </v-tabs>
</template>


</div>
