    <span class="hljs-tag">&lt;<span class="hljs-name">v-tabs</span> <span class="hljs-attr">active-tab-key</span>=<span class="hljs-string">"key1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">@tab-click</span>=<span class="hljs-string">"onTabClick"</span>&gt;</span>
