                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-tabs</span> <span class="hljs-attr">active-tab-key</span>=<span class="hljs-string">"tab1"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"editable-card"</span> <span class="hljs-attr">@add</span>=<span class="hljs-string">"onAdd"</span> <span class="hljs-attr">@remove</span>=<span class="hljs-string">"onRemove"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-tab-pane</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"tabPane in tabPanes"</span> <span class="hljs-attr">:tab-key</span>=<span class="hljs-string">"tabPane.key"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"tabPane.key"</span> <span class="hljs-attr">:tab</span>=<span class="hljs-string">"tabPane.tab"</span>&gt;</span><span>{{</span> tabPane.content <span>}}</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tab-pane</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-tabs</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">keyIndex</span>: <span class="hljs-number">1</span>,
                <span class="hljs-attr">tabPanes</span>: [
                    {<span class="hljs-attr">key</span>: <span class="hljs-string">'tab1'</span>, <span class="hljs-attr">tab</span>: <span class="hljs-string">'选项卡1'</span>, <span class="hljs-attr">content</span>: <span class="hljs-string">'选项卡1的内容'</span>},
                    {<span class="hljs-attr">key</span>: <span class="hljs-string">'tab2'</span>, <span class="hljs-attr">tab</span>: <span class="hljs-string">'选项卡2'</span>, <span class="hljs-attr">content</span>: <span class="hljs-string">'选项卡2的内容'</span>},
                    {<span class="hljs-attr">key</span>: <span class="hljs-string">'tab3'</span>, <span class="hljs-attr">tab</span>: <span class="hljs-string">'选项卡3'</span>, <span class="hljs-attr">content</span>: <span class="hljs-string">'选项卡3的内容'</span>}
                ]
            }
        },
        <span class="hljs-attr">methods</span>: {
            onAdd() {
                <span class="hljs-keyword">this</span>.tabPanes.push({
                    <span class="hljs-attr">key</span>: <span class="hljs-string">'newTab'</span> + <span class="hljs-keyword">this</span>.keyIndex,
                    <span class="hljs-attr">tab</span>: <span class="hljs-string">'新选项卡'</span> + <span class="hljs-keyword">this</span>.keyIndex,
                    <span class="hljs-attr">content</span>: <span class="hljs-string">'新选项卡'</span> + <span class="hljs-keyword">this</span>.keyIndex++ + <span class="hljs-string">'内容'</span>
                });
            },
            onRemove(tabKey) {
                <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-keyword">this</span>.tabPanes.length; i++) {
                    <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.tabPanes[i].key == tabKey) {
                        <span class="hljs-keyword">this</span>.tabPanes.splice(i, <span class="hljs-number">1</span>);
                        <span class="hljs-keyword">break</span>;
                    }
                }
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
