</demo-box>
<!--
::: demo
<summary>
  #### 屏蔽动画
  在某些特殊情况下可能不希望有动画，使用`animated: false`可以做到这一点。
</summary>

```html
<template>
    <v-tabs active-tab-key="1" :animated="false">
        <v-tab-pane tab-key="1" tab="选项卡一">选项卡一内容</v-tab-pane>
        <v-tab-pane tab-key="2" tab="选项卡二">选项卡二内容</v-tab-pane>
        <v-tab-pane tab-key="3" tab="选项卡三">选项卡三内容</v-tab-pane>
    </v-tabs>
</template>
```
:::
-->
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="tabs-props"><a class="header-anchor" href="#tabs-props" aria-hidden="true">¶</a> Tabs Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>activeTabKey</td>
<td>当前激活 tab 面板的 tabKey</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>slot : extra</td>
<td>tab bar 上额外的元素</td>
<td>slot node</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>页签的基本样式，可选 <code v-pre="">line</code>、<code v-pre="">card</code> <code v-pre="">editable-card</code> 类型</td>
<td>string</td>
<td><code v-pre="">line</code></td>
</tr>
<tr>
<td>size</td>
<td>大小，提供 <code v-pre="">default</code> 和 <code v-pre="">small</code> 两种大小，仅当 <code v-pre="">type=&quot;line&quot;</code> 时生效。</td>
<td>string</td>
<td><code v-pre="">default</code></td>
</tr>
<tr>
<td>position</td>
<td>页签位置，可选值有 <code v-pre="">top</code> <code v-pre="">right</code> <code v-pre="">bottom</code> <code v-pre="">left</code></td>
<td>string</td>
<td><code v-pre="">top</code></td>
</tr>
<tr>
<td>hideAdd</td>
<td>是否隐藏加号图标，在 <code v-pre="">type=&quot;editable-card&quot;</code> 时有效</td>
<td>boolean</td>
<td><code v-pre="">false</code></td>
</tr>
</tbody>
</table>
<!--
| animated | 是否使用动画切换 Tabs，在 `position=top|bottom` 时有效 | boolean | `true` |
-->
<h3 id="tabpane-props"><a class="header-anchor" href="#tabpane-props" aria-hidden="true">¶</a> TabPane Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>tabKey</td>
<td>对应 activeTabKey</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>tab</td>
<td>选项卡头显示文字</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>图标 Icon 类型</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用当前tab</td>
<td>boolean</td>
<td><code v-pre="">false</code></td>
</tr>
</tbody>
</table>
<h3 id="tabs-events"><a class="header-anchor" href="#tabs-events" aria-hidden="true">¶</a> Tabs Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>切换面板时触发的事件</td>
<td>—</td>
</tr>
<tr>
<td>tab-click</td>
<td>tab 被点击时触发的事件</td>
<td>—</td>
</tr>
<tr>
<td>add</td>
<td>新增页签的回调，在 <code v-pre="">type=&quot;editable-card&quot;</code>且<code v-pre="">hideAdd=false</code> 时有效</td>
<td>-</td>
</tr>
<tr>
<td>remove</td>
<td>新增页签的回调，在 <code v-pre="">type=&quot;editable-card&quot;</code> 时有效</td>
<td>tabKey</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
    #components-tabs-demo-card-top {
        background: #ECECEC;
        overflow: hidden;
        padding: 24px;
    }

    #components-tabs-demo-card-top > .card-container > .ant-tabs-card > .ant-tabs-content {
        height: 120px;
        margin-top: -16px;
    }

    #components-tabs-demo-card-top > .card-container > .ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane {
        background: #fff;
        padding: 16px;
    }

    #components-tabs-demo-card-top > .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
        border-color: transparent;
    }

    #components-tabs-demo-card-top > .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
        border-color: #fff;
    }
</style>
<script>
    export default {
        data() {
            return {
                position: 'top',
                positionOptions: [
                    {label: 'top', value: 'top'},
                    {label: 'bottom', value: 'bottom'},
                    {label: 'left', value: 'left'},
                    {label: 'right', value: 'right'}
                ],
                keyIndex: 1,
                tabPanes: [
                    {key: 'tab1', tab: '选项卡1', content: '选项卡1的内容'},
                    {key: 'tab2', tab: '选项卡2', content: '选项卡2的内容'},
                    {key: 'tab3', tab: '选项卡3', content: '选项卡3的内容'}
                ],
                customPosition: 'top',
                customActiveKey: 'tab1',
                customKeyIndex: 1,
                customTabPanes: [
                    {key: 'tab1', tab: '选项卡1', content: '选项卡1的内容'},
                    {key: 'tab2', tab: '选项卡2', content: '选项卡2的内容'},
                    {key: 'tab3', tab: '选项卡3', content: '选项卡3的内容'}
                ]
            }
        },
        methods: {
            onChange(tabKey) {
                console.log("tab changed : " + tabKey);
            },
            onTabClick(tabKey) {
                console.log("tab clicked : " + tabKey);
            },
            onAdd() {
                this.tabPanes.push({
                    key: 'newTab' + this.keyIndex,
                    tab: '新选项卡' + this.keyIndex,
                    content: '新选项卡' + this.keyIndex++ + '内容'
                });
            },
            onRemove(tabKey) {
                for (let i = 0; i < this.tabPanes.length; i++) {
                    if (this.tabPanes[i].key == tabKey) {
                        this.tabPanes.splice(i, 1);
                        break;
                    }
                }
            },
            onCustomAdd() {
                this.customActiveKey = 'newTab' + this.customKeyIndex;
                this.customTabPanes.push({
                    key: 'newTab' + this.customKeyIndex,
                    tab: '新选项卡' + this.customKeyIndex,
                    content: '新选项卡' + this.customKeyIndex++ + '内容'
                });
            },
            onCustomRemove(tabKey) {
                for (let i = 0; i < this.customTabPanes.length; i++) {
                    if (this.customTabPanes[i].key == tabKey) {
                        this.customTabPanes.splice(i, 1);
                        break;
                    }
                }
            }
        }
    }
</script>


